.hr {
  height:10px;
  background-color: #b8b8ff;
  border :0;
  margin:20px;
  
}

.header{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.title{
  text-align: center; 
  padding: 0px 0px 10px 0px;
  font-size: 50px;
  font-weight: 500px;
}
.todoSelect{
  margin :20px;
  padding:0px;
  width : 200px;
  height: 40px;
  text-align: center;
  background-color: white;
  border-radius: 5px;
  border: none;
  color: grey;
  font-size: 18px;
}

.todoOption{
  margin:0px;
  padding:0px;
  border-radius: 5px;
}


.container{
  margin: 0 auto;
  height: 1000px;
  width: 1000px;

}
.liContainer{
  padding: 1%;
  margin: 0px 20px 10px 20px;
  background-color: #b8b8ff;
  border-radius: 10px;
}

.heartTitle{
  font-size: 35px;
}

.toggleButton{
  margin:0px 0px 0px 20px;
  padding:10px;
  background-color:transparent ;
  border-radius: 10px;
  border:2px solid #ff8fa3;
  color:#ff8fa3;
  float:left;
  &:hover{
    background-color: #ff8fa3;
    color:whitesmoke;
  }
}

.loginButton{
  margin-right:20px;
  padding:11px;
  background-color: #ff8fa3;
  border-radius: 20px;
  border:none;
  color:white;
  float:right;
  &:hover{
    background-color: #f56882;
    color:whitesmoke;
  }
}